export default {
	data() {
		return {
			number: '',
			cool: '',
			way: '',
			tit: ''
		}
	},
	onLoad(option) {
		this.number = option.numb
		this.cool = option.cool
		this.way = option.way
		if (this.way == 1) {
			this.tit = '转让'
		} else {
			this.tit = '出售'
		}
	},
	methods: {
		btn: function() {
			uni.navigateBack({
				delta:1
			})
		}
	}
}
